import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import './Shop.css';

function Shop({ forumData }) {
  const [data, setData] = useState([]);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const result = await forumData();
        setData(result);
      } catch (error) {
        console.error(error);
      }
    };

    fetchData();
  }, [forumData]);
  // console.log(forumData);
  // console.log(data);

  return (
    <section className="text-gray-600 body-font mt-10">
      <div className="container mx-auto">
        <div className="containerX flex flex-wrap -m-2">
          {data.map((item, index) => {
            return (
              <div className="box" key={item.id}>
                <h2 className="name">{item.name}</h2>
                <Link to={`/Shop/Product/${item.id}`} className="buy">Buy</Link>
                <img alt="ecommerce" className="product" src={item.image} />
              </div>
            );
          })}
        </div>
      </div>
    </section>
  );
}

export default Shop;
